import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
        UserStatus : "Unknown",
        UserInformations: {},
    }
  },
  mutations :{
    UserRegisterF(state){
        state.UserStatus = 'Register'
    },
    UserUnknownF(state){
        state.UserStatus = 'Unknown'
    },
    UserLoginF(state){
        state.UserStatus = 'User'
    },
  },
  actions : {
  }
})

export default store