<template>
  <div class="login-container">
    <h2>로그인</h2>
    <form @submit.prevent="login">
      <div class="form-group">
        <label for="username">아이디:</label>
        <input
          type="text"
          style="width: 92%"
          id="userid"
          v-model="userid"
          name="userid"
          required
        />
      </div>
      <div class="form-group">
        <label for="password">비밀번호:</label>
        <input
          type="password"
          style="width: 92%"
          id="password"
          v-model="password"
          name="password"
          required
        />
      </div>
      <div class="form-group">
        <button type="submit">로그인</button>
      </div>
    </form>
    <p class="signup-link">
      계정이 없으신가요?
      <a
        style="text-decoration: underline; user-select: none"
        @click="$router.push('/Register')"
        >회원가입</a
      >
    </p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      userid: "",
      password: "",
    };
  },
  methods: {
    login() {
      axios
        .get(
          "https://hansung-ubs.github.io/Hansung-UBSP/data/" +
            this.userid +
            "_Uinfo.json"
        )
        .then((response) => {
          if (response.data.pw == this.password) {
            this.$store.UserStatus = "User";
            this.$store.commit('UserLoginF')
            this.$router.push('/home');
            
          } else {
            alert("아이디 혹은 비밀번호가 없거나 잘못되었습니다.");
          }
        })
        .catch(() => {
          // 오류 처리
          alert("아이디 혹은 비밀번호가 없거나 잘못되었습니다.");
        });
    },
  },
};
</script>

<style>
.login-container {
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 20px;
  width: 90%;
  max-width: 350px;
}

.login-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.form-group button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.form-group button:hover {
  background-color: #0056b3;
}

.signup-link {
  text-align: center;
  margin-top: 10px;
}
</style>

