    };
  },
  computed: {
    UserStat() {
      return new Date();
    },
  },
  watch: {
    $route(to) {
      if (to.path == "/Register") {
        this.$store.commit("UserRegisterF");
      } else if (this.$store.state.UserStatus == "Unknown") {
        this.$router.push("/Login");
      } else if (
        this.$store.state.UserStatus == "Register" &&
        to.path != "/Register"
      ) {
        this.$router.push("/Login");
        this.$store.commit("UserUnknownF");
      }
    },
  },
};
